import{polarToCartesian}from"../PolarUtils";import{getRadialCursorPoints}from"./getRadialCursorPoints";export function getCursorPoints(layout,activeCoordinate,offset){var x1,y1,x2,y2;if("horizontal"===layout)x2=x1=activeCoordinate.x,y1=offset.top,y2=offset.top+offset.height;else if("vertical"===layout)y2=y1=activeCoordinate.y,x1=offset.left,x2=offset.left+offset.width;else if(null!=activeCoordinate.cx&&null!=activeCoordinate.cy){if("centric"!==layout)return getRadialCursorPoints(activeCoordinate);var cx=activeCoordinate.cx,cy=activeCoordinate.cy,innerRadius=activeCoordinate.innerRadius,outerRadius=activeCoordinate.outerRadius,angle=activeCoordinate.angle,innerPoint=polarToCartesian(cx,cy,innerRadius,angle),outerPoint=polarToCartesian(cx,cy,outerRadius,angle);x1=innerPoint.x,y1=innerPoint.y,x2=outerPoint.x,y2=outerPoint.y}return[{x:x1,y:y1},{x:x2,y:y2}]}