import Decimal from"decimal.js-light";import{curry}from"./utils";function getDigitCount(value){return 0===value?1:Math.floor(new Decimal(value).abs().log(10).toNumber())+1}function rangeStep(start,end,step){for(var num=new Decimal(start),i=0,result=[];num.lt(end)&&i<1e5;)result.push(num.toNumber()),num=num.add(step),i++;return result}var interpolateNumber=curry((function(a,b,t){var newA=+a;return newA+t*(+b-newA)})),uninterpolateNumber=curry((function(a,b,x){var diff=b-+a;return(x-a)/(diff=diff||1/0)})),uninterpolateTruncation=curry((function(a,b,x){var diff=b-+a;return diff=diff||1/0,Math.max(0,Math.min(1,(x-a)/diff))}));export default{rangeStep:rangeStep,getDigitCount:getDigitCount,interpolateNumber:interpolateNumber,uninterpolateNumber:uninterpolateNumber,uninterpolateTruncation:uninterpolateTruncation};