import{sin,cos,sqrt,pi,tau}from"../math.js";const ka=.8908130915292852,kr=sin(pi/10)/sin(7*pi/10),kx=sin(tau/10)*kr,ky=-cos(tau/10)*kr;export default{draw(context,size){const r=sqrt(size*ka),x=kx*r,y=ky*r;context.moveTo(0,-r),context.lineTo(x,y);for(let i=1;i<5;++i){const a=tau*i/5,c=cos(a),s=sin(a);context.lineTo(s*r,-c*r),context.lineTo(c*x-s*y,s*x+c*y)}context.closePath()}};