export function getCursorRectangle(layout,activeCoordinate,offset,tooltipAxisBandSize){var halfSize=tooltipAxisBandSize/2;return{stroke:"none",fill:"#ccc",x:"horizontal"===layout?activeCoordinate.x-halfSize:offset.left+.5,y:"horizontal"===layout?offset.top+.5:activeCoordinate.y-halfSize,width:"horizontal"===layout?tooltipAxisBandSize:offset.width-1,height:"horizontal"===layout?offset.height-1:tooltipAxisBandSize}}